<template>
    <b-col>
        <b-breadcrumb class="bg-white">
            <b-breadcrumb-item to="/">
            <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>
             Home
            </b-breadcrumb-item>
            <b-breadcrumb-item active>{{ articleId ?'':''}}</b-breadcrumb-item>
        </b-breadcrumb>

        <ValidationObserver ref="observer" v-slot="{ invalid }">
            <b-form @submit="onSubmit">
                <b-card-group class="border shadow-sm p-1 mb-5">
                    <b-card-body>
                        <b-card class="border-0">
                            <b-card-text>
                                <b-card-title>
                                    <b-icon class="h1" :icon="titleIcon" variant="primary"></b-icon> 
                                    {{ articleId ?'':''}}
                                </b-card-title>
                                <hr>
                                <ValidationProvider name="" 
                                        rules="required" v-slot="{ valid,errors }">
                                    <b-form-group 
                                        label=": " 
                                        label-for="title"
                                        description=""
                                    >
                                        <b-form-input
                                            id="title"
                                            v-model="form.title"
                                            :state="errors[0] ? false : (valid ? true : null)"
                                            placeholder=""
                                            @input="saveTitle"
                                        ></b-form-input>
                                        <b-form-invalid-feedback id="inputLiveFeedback">{{ errors[0] }}</b-form-invalid-feedback>
                                    </b-form-group>
                                </ValidationProvider>

                                <ValidationProvider name="" 
                                        rules="required|max:1200" v-slot="{ valid,errors }">
                                    <b-form-group 
                                        label=": " 
                                    >
                                        <b-form-textarea
                                            id="content"
                                            v-model="form.content"
                                            placeholder=""
                                        ></b-form-textarea>
                                        <b-form-invalid-feedback id="inputLiveFeedback">
                                            {{ errors[0] }}</b-form-invalid-feedback>
                                    </b-form-group>
                                    
                                </ValidationProvider>

                                <b-button class="mt-4" :disabled="invalid" type="submit" 
                                    variant="primary" block>
                                    <b-icon icon="upload"></b-icon>  
                                </b-button>
                            </b-card-text>
                        </b-card>
                    </b-card-body>
                </b-card-group>
            </b-form>
        </ValidationObserver>
    </b-col>
</template>

<script>
import SimpleMDE from 'simplemde'
import hljs from 'highlight.js'
import ls from '@/utils/localStorage'

// 
window.hljs = hljs

// 
import { ValidationProvider, extend ,ValidationObserver} from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
import { messages } from 'vee-validate/dist/locale/zh_TW.json';

// rules
Object.keys(rules).forEach(rule => {
    extend(rule, {
         ...rules[rule], // copies rule configuration
         message: messages[rule] // assign message
    });
});


export default {
    name: 'Create',
    data() {
        return {
            form:{
                title: '',
                content: '',
            },
            // 
            articleId: undefined,
        }
    },
    computed:{
        titleIcon(){
            return this.articleId == undefined  ? 'file-plus':'file-diff'
        }

    },
    //  articleId
    //  vm  this
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.setArticleId(vm.$route.params.articleId)
        })
    },
    // 
    //  this
    beforeRouteLeave(to, from, next) {
        this.clearData()
        next()
    },
    watch: {
        // 
        //  '$route'
        //  Vue 
        //  beforeRouteEnter 
        '$route'(to) {
            this.clearData()
            this.setArticleId(to.params.articleId)
        }
    },
    mounted() {
        const simplemde = new SimpleMDE({
            element: document.querySelector('#content'),
            placeholder: ' Markdown ',
            spellChecker: false,
            autoDownloadFontAwesome: false,
            autosave: {
                enabled: true,
                uniqueId: 'vuejs-forum-demo'
            },
            renderingConfig: {
                //  highlight.js
                codeSyntaxHighlighting: true
            }
        })
        //  change 
        simplemde.codemirror.on('change', () => {
            // 
            this.form.content = simplemde.value()
        })
        //  simplemde 
        this.simplemde = simplemde
        // 
        this.initContent()
    },
    methods: {
        // @input
        saveTitle() {
            ls.setItem('smde_title', this.form.title)
        },
        initContent(articleId) {
            const simplemde = this.simplemde
            const smde_title = ls.getItem('smde_title')

            //  articleId 
            if (articleId !== undefined) {
                // 
                const article = this.$store.getters.getArticleById(articleId)

                if (article) {
                    // 
                    const { title, content } = article

                    // 
                    this.form.title = smde_title || title

                    // 
                    this.form.content = simplemde.value() || content

                    // 
                    simplemde.value(this.form.content)
                }
            } else { 
                //  articleId 
                this.form.title = smde_title
                this.form.content = simplemde.value()
            }

        },
        onSubmit(evt) {
            evt.preventDefault()
            const title = this.form.title
            const content = this.form.content

            if (title !== '' && content.trim() !== '') {
                const article = {
                    title,
                    content
                }

                this.$store.dispatch('post', { article, articleId: this.articleId })
                this.clearData()
            }
        },
        clearData() {
            this.form.title = ''
            ls.removeItem('smde_title')
            this.simplemde.value('')
            this.simplemde.clearAutosavedValue()
        },
        setArticleId(articleId) {
            //  localStorage  articleId
            const localArticleId = ls.getItem('articleId')
            //  /articles/1/edit  /articles/2/edit
            // 
            if (articleId !== undefined && !(articleId === localArticleId)) {
                this.clearData()
            }

            this.articleId = articleId
            this.initContent(articleId)
            ls.setItem('articleId', articleId)
        }
    },
    components: {
        ValidationProvider,ValidationObserver
    }
}

</script>

<style scoped>
    form,h4 { font-weight:700 }
    .b-icon.bi {vertical-align: middle;}

</style>


